import { Component } from '@angular/core';
import {GetUserInfoService} from "./services/get-user-info.service";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
    title = 'app';
    link:string = '';
    oauthToken:any = '';
    fullUserAlbumsData:any = null;
    userInfo:any = null;
    userName:any = null;
    constructor(private _getUserInfoService:GetUserInfoService) {

    }

    ngOnInit() {
        this.getKey();
    }

    getKey() {
        this._getUserInfoService.getKey().subscribe((data) => {
                console.log("DATA", data);
                this.oauthToken = data["oauth_token"];
                this.link = "https://www.flickr.com/services/oauth/authorize?perms=write&oauth_token=" + this.oauthToken;
                console.log("this.link", this.link);
                /*this._getUserInfoService.getUserInfo();*/
                console.log("USER INFO");
            },
            (error)=> {
                console.log("ERROR", error);
            });
    }

    getUserAlbums() {
        this._getUserInfoService.getUserPhotosetsList().subscribe((data)=> {
                this.fullUserAlbumsData = data;
            if(this.fullUserAlbumsData.stat === 'ok'){
                console.log("getUserAlbums INFO", this.fullUserAlbumsData);
                console.log("Status", this.fullUserAlbumsData.stat);
                console.log("USER ALBUMS", this.fullUserAlbumsData.photosets.photoset);
            }
            else{
                console.log("Smtg wrong...");
                console.log(this.fullUserAlbumsData.message);
            }
            },
            (error)=> {
                console.log(error)
            });
    };

    getUserInfo() {
        this._getUserInfoService.getUserInfo().subscribe((data)=> {
                this.userInfo = data;
            if(this.userInfo.stat === 'ok'){
                this.userName = this.userInfo.person.username._content;
                console.log("getUserInfo INFO",data);
            }
            else{
                console.log("Smtg wrong...");
                console.log(this.userInfo.message);
            }
        },
            (error)=>{
            console.log(error)
        })
    }
}




