import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {HttpClientModule} from "@angular/common/http";

import { AppComponent } from './app.component';
import {GetUserInfoService} from "./services/get-user-info.service";
import { UserInfoComponentComponent } from './userInfoComponent/user-info-component/user-info-component.component';


const appRoutes: Routes =[
    { path: '', component: AppComponent},
    { path: 'user-info', component: UserInfoComponentComponent}
];


@NgModule({
  declarations: [
    AppComponent,
    UserInfoComponentComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    RouterModule.forRoot(appRoutes)
  ],
  providers: [GetUserInfoService],
  bootstrap: [AppComponent]
})
export class AppModule { }



